// use strict

import { createSVG } from '../util/Util';

export function getSvgs() {
  const audioSvg = createSVG('svg', ['daily-card__icon', 'daily-card__icon_audio']);
  const audioUse = createSVG('use', [], [['href', '#stats_audio-icon']]);
  audioSvg.append(audioUse);

  const sprintSvg = createSVG('svg', ['daily-card__icon', 'daily-card__icon_sprint']);
  const sprintUse = createSVG('use', [], [['href', '#stats_sprint-icon']]);
  sprintSvg.append(sprintUse);

  return [audioSvg, sprintSvg];
}

export function getHiddenSvgs() {
  const hiddenSvg = createSVG('svg', [], [['display', 'none']]);
  hiddenSvg.append(createAudioSymbol(), createSprintSymbol());
  return hiddenSvg;
}

