import { createSVG } from '../../util/Util';

export function getHiddenSvgs() {
  const svgHidden = createSVG('svg', [], [['display', 'none']]);
  svgHidden.append(
    createSymbolShackespeare(),
    createSymbolUSA(),
    createSymbolPhone(),
    createSymbolNumber(),
    createSymbolSmile(),
    createSymbolTime(),
    createSymbolArrHor(),
    createSymbolArrVert(),
    createSymbolPeople(),
    createSymbolLetters(),
    createSymbolBook(),
    createSymbolAirplane(),
  );
  return svgHidden;
}

export function getSvgs() {
  const classArr = [
    'info_shakespeare-icon',
    'info_usa-icon',
    'info_phone-icon',
    'info_number-icon',
    'info_smile-icon',
    'info_time-icon',
    'info_arr-hor-icon',
    'info_arr-vert-icon',
    'info_people-icon',
    'info_letters-icon',
    'info_book-icon',
    'info_airplane-icon',
  ];
  const svgs: SVGElement[] = [];
  createSvgs(classArr, svgs);
  return svgs;
}

function createSvgs(arr: string[], result: SVGElement[]) {
  for (let i = 0; i < arr.length; i++) {
    const svg = createSVG('svg', ['info-list__item_icon']);
    const use = createSVG('use', [], [['href', `#${arr[i]}`]]);
    svg.append(use);
    result.push(svg);
  }
}

